import { SET_TOUR_DATA, SET_CURRENT_CLIP_DATA } from '../types';

const initialState = {
  tour: {},
  currentClip: {}
};

export default function(state = initialState, { type, payload }) {
  switch (type) {
    case SET_TOUR_DATA:
      return {
        ...state,
        tour: payload,
        tourIsLoaded: true
      };
    case SET_CURRENT_CLIP_DATA:
      return {
        ...state,
        currentClip: payload
      };
    default:
      return state;
  }
}
