import { connect } from "react-redux";
import { fetchDB, setCurrentClip } from "../redux/actions/dataActions";
import store from "../redux/store";

import Slider from "../views/slider";

const mapStateToProps = (state) => ({
  tour: state.data.tour,
  clip: state.data.currentClip,
});

const mapDispatchToProps = (dispatch) => ({
  fetchDB: (payload) => fetchDB(dispatch, payload),
  setCurrentClip: (payload) => setCurrentClip(dispatch, store, payload),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Slider);
