export function map(obj, action) {
  for (let key in obj) {
    action(obj[key]);
  }
}

export function mapKV(obj, action) {
  for (let key in obj) {
    action(key, obj[key]);
  }
}

export function asArray(obj) {
  let array = [];
  for (let key in obj) {
    array.push({ key, value: obj[key] });
  }
  return array;
}

export function firstChild(obj) {
  for (let key in obj) {
    return obj[key];
  }
  return "";
}
