import React, { useState, useEffect, useMemo } from "react";
import Swiper from "react-id-swiper";

import Screen2d from "../../components/Screen2d";

import { asArray } from "../../utils/map";
import iconArrow from "./../../assets/img/icon-arrow.png";
import iconVR from "./../../assets/img/icon-vr.png";
import logo from "./../../assets/img/logo.png";

const imgSrc =
  "https://image.shutterstock.com/image-photo/large-beautiful-drops-transparent-rain-260nw-668593321.jpg";

function Slider({
  match: {
    params: { tourName },
  },
  tour: { images } = {},
  clip: { name, url, pins } = {},
  fetchDB,
  setCurrentClip,
}) {
  const [gallerySwiper, getGallerySwiper] = useState(null);
  const [thumbnailSwiper, getThumbnailSwiper] = useState(null);
  const [thumbnailToggler, getThumbnailToggler] = useState(null);

  console.log("thumbnailToggler", thumbnailToggler);

  useEffect(() => {
    fetchDB(tourName);

    console.log(window);

    // if (window.constant.safeAreaInsetRight) {
    //   var notch = true;
    //   console.log(notch);
    // }
  }, []);

  useEffect(() => {
    if (
      gallerySwiper !== null &&
      gallerySwiper.controller &&
      thumbnailSwiper !== null &&
      thumbnailSwiper.controller
    ) {
      gallerySwiper.controller.control = thumbnailSwiper;
      thumbnailSwiper.controller.control = gallerySwiper;
    }
  }, [gallerySwiper, thumbnailSwiper]);

  const gallerySwiperParams = useMemo(
    () => ({
      containerClass: "is-scene-container swiper-container",
      slideClass: "is-sceneasd",
      getSwiper: getGallerySwiper,
      spaceBetween: 10,
      simulateTouch: false,
      noSwiping: true,
    }),
    [],
  );

  const thumbnailSwiperParams = useMemo(
    () => ({
      wrapperClass: "swiper-wrapper is-thumb-wrapper",
      slideClass: "is-thumb",
      getSwiper: getThumbnailSwiper,
      spaceBetween: 0,
      centeredSlides: true,
      slidesPerView: "auto",
      touchRatio: 0.2,
      slideToClickedSlide: true,
    }),
    [],
  );

  return (
    <div className="is-wrapper">
      <div className="is-header">
        <img src={logo} alt="Skyward's logo" />
      </div>
      <Swiper {...gallerySwiperParams}>
        {asArray(images).map(({ key }) => (
          <Screen2d
            key={key}
            imageURL={url}
            pins={pins}
            onPinClick={() => setCurrentClip(key)}
          />
        ))}
      </Swiper>
      <div
        className={`is-thumb-container ${thumbnailToggler ? "is-hidden" : ""}`}
      >
        <div className="thumbnails-header">
          <div
            className="is-btn is-btn-toggle"
            onClick={() => getThumbnailToggler(!thumbnailToggler)}
          >
            <img className="icon-arrow" src={iconArrow} alt="Arrow toggle" />
          </div>
          <a className="is-btn" href="#">
            <img className="icon-vr" src={iconVR} alt="Virtual reality" />
          </a>
        </div>
        <Swiper {...thumbnailSwiperParams}>
          {asArray(images).map(({ key, value }) => (
            <div
              key={key}
              onClick={() => setCurrentClip(key)}
              className={key === name ? "is-active" : ""}
            >
              <p>{value.name}</p>
              <img src={imgSrc} alt={`${value.name}'s`} />
            </div>
          ))}
        </Swiper>
      </div>
    </div>
  );
}

export default Slider;
