import { SET_TOUR_DATA, SET_CURRENT_CLIP_DATA } from "../types";
import { db } from "../../firebase/firebase";
import { firstChild } from "../../utils/map";

export const fetchDB = (dispatch, tourName) => {
  if (!tourName) return;
  db.ref("/dictionary/")
    .orderByChild("name")
    .equalTo(tourName)
    .once("value")
    .then(function(snapshot) {
      const tour = firstChild(snapshot.val());
      dispatch({ type: SET_TOUR_DATA, payload: tour });
    });
};

export const setCurrentClip = (dispatch, store, clipID) => {
  const clipData = store.getState().data.tour.images[clipID];
  dispatch({ type: SET_CURRENT_CLIP_DATA, payload: clipData });
};
