import React from "react";
import { Pannellum } from "pannellum-react";
import { asArray } from "../utils/map";

const Screen2d = ({ imageURL, pins, onPinClick }) => (
  <div>
    <Pannellum
      width="100%"
      height="100%"
      image={imageURL}
      pitch={10}
      yaw={180}
      hfov={110}
      autoLoad
    >
      {pins &&
        asArray(pins).map(({ key, value }) => (
          <Pannellum.Hotspot
            key={key}
            type="info"
            pitch={value.pos.x}
            yaw={value.pos.y}
            handleClick={() => onPinClick(key)}
            text={value.name}
          />
        ))}
    </Pannellum>
  </div>
);

export default Screen2d;
